import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef, MatDialog } from '@angular/material/dialog';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import { ApplicationService } from 'src/app/services/app.service';
import { generateBookingForm, statusFromBookings } from 'src/app/services/data/bookings/booking.utilities';
import { Space } from 'src/app/services/data/spaces/space.class';
import { BaseDirective } from 'src/app/shared/base.directive';
import { UsersService } from 'src/app/services/data/users/users.service';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';

import * as dayjs from 'dayjs';
import { RequirementDetailsModalComponent, RequirementDetailsModalData } from 'src/app/shell/bookings/overlays/requirement-details-modal/requirement-details-modal.component';
import { DialogEvent } from 'src/app/shared/utilities/types.utilities';

export interface ExploreBookingModalData {
    /** Unix epoch in ms of the booking start time */
    date: number;
    /** Space to make the booking in */
    space: Space;
    /** List of bookings for the active space at the current time */
    bookings: Booking[];
}

@Component({
    selector: 'a-explore-booking-modal',
    templateUrl: './booking-modal.component.html',
    styleUrls: ['./booking-modal.component.scss']
})
export class ExploreBookingModalComponent extends BaseDirective implements OnInit {
    /**  */
    public booking: Booking;
    /** Form fields for new ad-hoc booking */
    public form: FormGroup;
    /** Form contents to display */
    public state: 'info' | 'form' = 'info';
    /** Description of the current loading state */
    public loading: string;
    /** String of time until next booking */
    public available_until: string;
    /** Whether booking was successful */
    public success: boolean;

    public get is_requesting(): boolean {
        const booking = this.booking.toJSON();
        return this.space.byRequest({
            date: this.booking.date || dayjs(this._data.date).valueOf(),
            duration: this.booking.duration || 30,
            host: booking.organiser || this._users.current
        });
    }

    /** Display string for the selected date */
    public get date(): string {
        const date = dayjs(this._data.date);
        return date.format('DD MMM YYYY');
    }

    /** Display string for the selected time */
    public get time(): string {
        const date = dayjs(this._data.date);
        return date.format('h:mm A');
    }

    /** Selected space */
    public get space(): Space {
        return this._data.space;
    }

    /** Whether space is being booked for now */
    public get is_now(): boolean {
        const now = dayjs();
        const date = dayjs(this._data.date);
        return Math.abs(now.diff(date, 'm')) < 5;
    }

    constructor(
        private _dialog_ref: MatDialogRef<ExploreBookingModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: ExploreBookingModalData,
        private _router: Router,
        private _dialog: MatDialog,
        private _service: ApplicationService,
        private _users: UsersService,
        private _spaces: SpacesService
    ) {
        super();
    }

    public ngOnInit(): void {
        const date = dayjs(this._data.date);
        this.booking = new Booking({
            title: 'Ad-Hoc Booking',
            date: date.valueOf(),
            duration: 30,
            room_ids: [this._data.space.id]
        });
        const status = statusFromBookings(
            this._data.bookings,
            this.space.bookable,
            this.space.byRequest({
                date: this._data.date,
                duration: 30,
                host: this._users.current
            }),
            this._data.date
        );
        this.available_until = status.available_until;
    }

    /**
     * Move onto the next step of the booking flow
     */
    public next() {
        if (!this.is_now) {
            const booking = this.booking.toJSON();
            localStorage.setItem('ACA_STAFF.booking_form_data', JSON.stringify({ ...booking, id: 'ad-hoc' }));
            console.log('Booking:', booking);
            this._router.navigate(['/book', 'space'], { queryParams: { back: '/explore' } });
            this._dialog_ref.close();
        } else {
            this.form = generateBookingForm(this.booking, [
                'space_list',
                'organiser',
                'title',
                'duration'
            ]);
            this.state = 'form';
        }
    }

    public book() {
        /* istanbul ignore else */
        if (this.form.valid) {
            this.updateRequirements().then(() => this.makeBooking());
        }
    }

    private updateRequirements() {
        return new Promise((resolve, reject) => {
            const space_list = [...this.form.controls.space_list.value];
            if (
                this.form.controls.recurrence?.value?.period &&
                this.form.controls.recurrence?.value?.conflicts?.length > 0
            ) {
                this.form.controls?.recurrence?.value?.conflicts?.forEach(i => {
                    if (!space_list.find(space => space.email === i.space)) {
                        space_list.push(this._spaces.find(i.space));
                    }
                });
            }
            let resolved = false;
            const ref = this._dialog.open<
                RequirementDetailsModalComponent,
                RequirementDetailsModalData
            >(RequirementDetailsModalComponent, {
                maxWidth: '95vw',
                width: '32em',
                data: {
                    spaces: space_list,
                    notes_field: this.form.controls.notes,
                    codes_field: this.form.controls.equipment_codes,
                    head_counts_field: this.form.controls.expected_attendees,
                },
            });
            this.subscription(
                'modal_events',
                ref.componentInstance.event.subscribe((event: DialogEvent) => {
                    /* istanbul ignore else */
                    if (event.reason === 'done') {
                        resolve();
                        resolved = true;
                        ref.close();
                    }
                })
            );
            ref.afterClosed().subscribe(() => {
                if (!resolved) {
                    reject();
                }
            });
        });
    }

    private makeBooking() {
        this.loading = 'Checking space availability...';
        this.checkSpaceAvailability().then(
            () => {
                const new_booking = new Booking({
                    ...this.booking,
                    ...this.form.value,
                    room_ids: [this._data.space.id]
                });
                new_booking.save().then(
                    () => {
                        this.loading = null;
                        this.success = true;
                        this.timeout('close', () => this._dialog_ref.close(), 5000);
                    },
                    err => {
                        this.loading = null;
                        this._service.notifyError(
                            `Error ${this.booking.id ? 'updating' : 'creating'} booking: ${err.message || err}}`
                        );
                    }
                );
            },
            err => {
                this.loading = null;
                this._service.notifyError(`Error checking space availability: ${err.message || err}`);
            }
        );
    }

    /**
     * Check whether the selected spaces are available for the selected time period
     */
    private checkSpaceAvailability(): Promise<void> {
        const spaces: Space[] = this.booking.space_list;
        return new Promise((resolve, reject) => {
            this._spaces.available({
                room_ids: spaces.map(space => space.id).join(','),
                date: this.booking.date,
                duration: this.booking.duration
            }).then(
                space_list => {
                    for (const space of space_list) {
                        if (!space.was_available) {
                            return reject(
                                `${space.name} is not available at the select time period.`
                            );
                        }
                    }
                    resolve();
                },
                err => reject(err)
            );
        });
    }
}
